//  IMPORTING LIBRARIES
import React from "react";
import ReactMapGl, { Marker } from "react-map-gl";
import * as infect_data from "../infect_data.json";  // IMPORTING JSON

console.log(infect_data.infected[0].location[0].latitude);

const Api_token =
  "pk.eyJ1IjoicmVzb25hbnRjaGFvczIyIiwiYSI6ImNrOGNrdGllMzBtdmYzZm9hbmhybm5xMDkifQ.wNnfoAL2YsIUsUH7clsjRA";

//  DEFINING COMPONENT
class Map extends React.Component {
  state = {
    viewport: {
      width: "100%",
      height: "100vh",
      latitude: 0,
      longitude: 0,
      zoom: 15
    },
    errorMsg: ""
  };

  componentDidMount() {
    window.navigator.geolocation.getCurrentPosition(
      Position =>
        this.setState({
          viewport: {
            width: "100%",
            height: "100vh",
            latitude: Position.coords.latitude,
            longitude: Position.coords.longitude,
            zoom: 12
          }
        }), //To update state , always use setState
      err => this.setState({ errorMsg: err.message }) //To update state , always use setState,
    );
  }

  render() {
    return (
      <div>
        <ReactMapGl
          {...this.state.viewport}
          onViewportChange={viewport => {
            console.log(viewport);
            this.setState({ viewport });
          }}
          mapStyle="mapbox://styles/resonantchaos22/ck8cmgw6w209j1in4829g1orj"
          mapboxApiAccessToken={Api_token}
        >
          {infect_data.infected.map(infected_person => (
            <Marker
              key={infected_person.id}
              latitude={infected_person.location[0].latitude}
              longitude={infected_person.location[0].longitude}
            >
              <i
                style={{ width: "15%" }}
                className="big circle outline icon"
              ></i>
              <p style={{ color: "red" }}>infection exists!!</p>
            </Marker>
          ))}
        </ReactMapGl>
      </div>
    );
  }
}

//  EXPORTING COMPONENT
export default Map;
